@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

.colorfull{
    color: #1de5ac;
}
.colorfullYellow{
    color: #f5db00;
}



.question {
    position: relative;
    background-color: #ffffff;
    color: #000000;
    width: 100%;
    max-width: 800px;
    padding: 50px;
    padding-top: 26px;
    margin: 30px auto;
    backdrop-filter: blur(10px);
    font-size: 18px;
    font-weight: 400;
    box-shadow: 0 14px 34px #BDA575;
    line-height: 1.3636363636;
    border-radius: 20px;
    box-sizing: border-box;
}

.df {
   display: flex;
    justify-content: center;
}
.ornament{
    width: calc(100% + 100px);
    margin-left: -50px;
}
.ornament img{
    width: 100%;
}

.header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: #fff;
}
.container{
    max-width: 1250px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.logo, .logoHmarochos{
    /*height: 35px;*/
    display: flex;
    padding: 0 30px;
    justify-content: center;
    color: #003a5d;
    text-decoration: none;
    text-align: left;
    font-size: 14px;
    border: none;
    font-family: 'Open Sans', serif;;
    height: 100%;
    background-color: white;
}
.logo:hover{
    text-decoration: underline;
    cursor: pointer;
}

.logo img{
    height: 35px;
    margin-left: 17px;
}

.logoHmarochos img{
    width: 160px;
}

.logo{
    align-items: center;
    line-height: 1;
    text-transform: uppercase;
}

.logoHmarochos{
    /*padding: 20px 30px;*/
    /*height: 50px;*/
    display: flex;
    align-items: center;
}




.questionContainer {
    position: relative;
    min-height: 100vh;
    /*min-height: -webkit-fill-available;*/
    padding-top: 130px;
    box-sizing: border-box;
    display: flex;
    align-items: flex-start;
    background-size: cover;
    background-color: #28281f;
    color: #000000;
    transition: opacity 1s ease-in-out, backdrop-filter 1s ease-in-out;
    background-attachment: fixed;
}

.blur{
    background-color: rgb(255,243,216,0.41);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 1s ease-in-out;
}

.social{
    display: flex;
    justify-content: center;
}

.question .answerContainer {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    flex-wrap: wrap;
    transition: opacity 1s ease-in-out;
    align-items: stretch;
    align-content: stretch;
    /*align-content: stretch*/
    /*margin-left: 90px;*/
}


.question .answer {
    width: 50%;
    transition: opacity 1s ease-in-out;
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
}


.question .answer button, .button {
    color: #003B5D;
    width: 100%;
    min-height: 120px;
    max-width: 300px;
    font-size: 18px;
    padding: 16px;
    text-align: center;
    display: flex;
    align-items: center;
    border: none;
    background-color: #CCD8DF;
    border-radius: 20px;
    cursor: pointer;
    transition: 0.5s;
    font-family: "Open Sans", sans-serif;
    margin: 0 auto;
    font-weight: 700;
    justify-content: center;
    box-sizing: border-box;
    /* border: 1px solid transparent */
}

.question .answer button.rightAnswer, .button.rightAnswer {
    background-color: rgb(241,196,105);
    color: rgb(93, 39, 0);
    text-transform: uppercase;
}

.question .answer button.wrongAnswer, .button.wrongAnswer {
    background-color: rgba(241, 105, 105, 0.66);
    color: rgb(93, 39, 0);
}

.question .answer button.withImage .image{
    height: 100px;
    margin-right: 15px;
}
.question .answer button.withImage .icon{
    display: none;
}


.question .answer button:hover,  .button:hover {

    transition: 0.5s ease-in-out;
    box-shadow: 0px 14px 34px rgba(104, 142, 165, 0.3);
    /* border: 1px solid #1de5ac */
}
.question .answer button.rightAnswer:hover,  .button:hover.rightAnswer {

    transition: 0.5s ease-in-out;
    box-shadow: 0px 14px 22px #bda5756e;
    /* border: 1px solid #1de5ac */
}

.question .answer button:disabled:hover, .button:disabled:hover{
    box-shadow: none;
}

.question .answer button.startButton, .button.startButton{
    min-height: auto;
    font-size: 28px;
}


/* .question .answer button:hover span{
    transform: translateX(30px);
    transition: 1s ease-in-out;

} */

.question .icon {
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(255,255,255, 0.7);
}

.questionText {
    text-align: left;
    width: 100%;
    padding: 30px 0;
    padding-top: 47px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.questionText a:hover{
    color: rgb(241,196,105);
}
.questionText span{
    color: rgb(241,196,105);
}

.questionText img{
    width: 100%;
    margin-top: 20px;
    border-radius: 10px;
}

.accent {
    color: #f5db00;
}

.rate{
    max-width: 300px;
    margin: 20px auto;
}
.rateText{
    margin-top: 20px;
    font-weight: bold;
}
.stars{
    /*color: #1de5ac;*/
    margin-bottom: 20px;
}

.question .characterWindow {
    width: 47%;

}
.question .characterWindow button{
    flex-direction: column;
    text-transform: none;
    height: 100%;
}

.question .characterWindow button .image{
    margin-bottom: 15px;
    height: 150px !important;
    margin-left: 0;

}
.question .characterWindow button b{
    font-weight: bold;
    color: #1de5ac;
}
.question .characterWindow button p{
    margin: 7px 0;
}

.resultContainer{
   max-width: 300px;
    margin-left: 15px;
}
.result{
    justify-content: center;
    padding: 40px;
}
.result .image{
    max-height: 200px;
}
.resultStarts{
   margin: 15px 0;
}

.resultText{
    margin-bottom: 15px;
    margin-top: 30px;
    display: block;
}
.resultText .author{
    display: block;
    font-size: 14px;
    color: rgba(0, 58, 93, 0.67);
}
.resultText b{
    color: #1de5ac;
    text-transform: uppercase;
}


.modal{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 400px;
    background-color: #ffffff;
    color: #000000;
    border: 2px solid #f5db00;
    border-radius: 25px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Open Sans', serif;
    box-sizing: border-box;
}

.modal img{
    height: 50px;
    margin-bottom: 30px;
}

.modal .button{
    text-align: center;
    justify-content: center;
}

.loader {
    height: 100vh;
    width: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255);
    backdrop-filter: blur(5px);
    opacity: 1;
    z-index: 20;
    top: 0;
    left: 0;
}
.loader img{
    max-height: 80px;
}

.nowrap{
    flex-wrap: nowrap !important;
}

.car__body {
    animation: shake 0.2s ease-in-out infinite alternate;
}

.carLine {
    transform-origin: center right;
    stroke-dasharray: 22;
    animation: line 0.8s ease-in-out infinite;
    animation-fill-mode: both;
}

.carLineTop {
    animation-delay: 0s;
}

.carLineMiddle {
    animation-delay: 0.2s;
}

.carLineBottom {
    animation-delay: 0.4s;
}


.questionText p{
    margin: 10px 0 0 0;
}

.questionText p a{
    color: #003a5d;
}

@keyframes shake {
    0% {
        transform: translateY(-1%);
    }

    100% {
        transform: translateY(3%);
    }
}

@keyframes line {
    0% {
        stroke-dashoffset: 22;
    }

    25% {
        stroke-dashoffset: 22;
    }

    50% {
        stroke-dashoffset: 0;
    }

    51% {
        stroke-dashoffset: 0;
    }

    80% {
        stroke-dashoffset: -22;
    }

    100% {
        stroke-dashoffset: -22;
    }
}

.finish{
    background-color: #18a980 !important ;
}


.resultText img{
    max-height: 150px;
    margin: 30px;
}

.question .car{
    margin-bottom: 30px;
}

.question .logo {
    margin-left: auto;
    padding-right: 0;
}

@media screen and (max-width: 980px) {
    .question,  .header {
        padding: 20px;
        margin: 20px;
    }
    .question{
        padding-top: 30px;
    }

    .ornament {
        width: calc(100% + 40px);
        margin-left: -20px;
    }
   .header {
       box-sizing: border-box;
       margin: 0;
       justify-content: space-between;
       top: 0;
    }

   .logo{
       /*flex-wrap: wrap;*/
       justify-content: left;
       align-items: center;
       font-family: 'Open Sans', serif;;
       padding: 0 30px;
       /*height: auto;*/
       /*max-width: 105px;*/
   }
   .logo img{
       height: 30px;
       margin-bottom: 17px;
   }

   .plash{
       display: none;
   }

    .questionContainer{
        padding-top: 150px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center top;
        background-attachment: fixed;
    }
}

