        /*border-radius: 0;*/
        /*background-color: transparent;*/
        /*backdrop-filter: none;*/
    }
    .logo{
        flex-wrap: wrap;
        height: auto;
        justify-content: right;
        width: 160px;

    }
    .logo span{
        margin-top: 2px;
        text-align: right;
        order: 2;
        font-size: 10px;
        line-height: 14px;
        color: #000000;
    }
    .logo img{
        height: 25px;
    }
    .logo.nowrap span{
        text-align: center;
        white-space: nowrap;

    }
    .logo img, .logoHmarochos img {
        width: 90%;
        height: auto;
        margin: 0;
    }
    .logo img{
        height: 25px;
        width: auto;
    }
        /*margin: 0 0 10px 0;*
    }

    .header{
        height: 100px;
        padding: 0;
        align-items: stretch;
    }
    /*.header{*/
    /*    align-items: stretch;*/
    /*    padding: 0;*/
    /*    margin: 0;*/
    /*    background-color: rgba(0, 0, 0, 0.8);*/
    /*    border-radius: 15px;*/
    /*    margin-top: 15px;*/
    /*}*/

    .question{
        padding: 20px;
        border-radius: 20px;
    }

    .question .answer{
        margin-top: 20px;
        padding-top: 0;
        width: 100%;
    }
    .question .answer:first-child {
        margin-top: 0;
    }

    .question .characterWindow {
        width: 100%;

    }

    .question .characterWindow button{
        padding-top: 0;
        margin-top: 20px;
