.nextButton{
    background-color: inherit;
    border: none;
    font: inherit;
    color: #003a5d;
    cursor: pointer;
    margin-top: 20px;
    margin-left: auto;
    font-weight: bold;
    transition: 0.5s ease-in-out;
}

.nextButton svg{
    margin-left: 10px;
    fill: #003a5d;
    transition: 0.5s ease-in-out;
}

.nextButton:hover {
    color: rgb(241,196,105);
}

.nextButton:hover svg {
    fill: rgb(241,196,105);
}
