@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

.App {
  text-align: center;
  background-color: #ffffff;
  font-family: 'Open Sans', serif;;


}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}


/* React SwitchTransition Animation CSS Rules */
.card {
  /* padding: 15px;
  margin: 15px 100px; */
  /* border: 2px solid rgba(0, 0, 0, .9); */
  /* background-color: rgba(50, 70, 150, .5); */
  /* color: rgba(50, 70, 150, .9); */
}
.card-enter {
  opacity: 0;
}
.card-enter-active {
  opacity: 1;
  transition: opacity 1s;
}
.card-exit {
  opacity: 0;
}
.card-exit-active {
  opacity: 1;
  transition: opacity 1s;
}

.gra{
  font-family: 'Caveat', cursive;
  color: #003a5d;
  text-align: center;
  font-size: 62px;
  line-height: 70px;
  display: block;
  width: 100%;
}



.gra__under{
  text-align: center;
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 53px;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
  display: block;
}


@media screen and (max-width: 980px) {
  .gra{
    font-size: 38px;
    line-height: 40px;
    text-align: left;
  }
  .gra__under{
    font-size:16px;
    line-height: 22px;
    text-align: left;
    margin-bottom: 30px;
  }
}
